@use 'assets/font_instrument-sans';
@use 'screen_queries/changelog/768' as *;
@use 'screen_queries/changelog/1100' as *;

/* animations */
header, main, footer {
  opacity: 0;
  animation: fadeInPage .5s;
  animation-fill-mode: forwards;
}

@keyframes fadeInPage {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

* {
  scrollbar-width: thin;
}

html {
  scrollbar-color: rgb(184, 140, 184) transparent;
}

body {
  font-family: 'Instrument Sans', sans-serif;
  font-size: 1.1em;
  line-height: 1.5em;
}

a {
  background-color: transparent;
  transition: all 0.2s ease-out;
  text-decoration-style: dashed;
  text-underline-offset: 3px;
  text-decoration-thickness: 1px;
  &:hover {
    background-color: palevioletred;
    text-decoration: none;
    color: white !important;
  }
}

button {
  cursor: pointer;
}

.layout {
  display: flex;
  max-width: min-content;
  margin: auto;
  column-gap: 2em;
}

h1, h2, h3 {
  font-weight: 500;
}

header {
  margin: 50px auto auto auto;
  padding-left: 50px;
  width: 15vw;
  position: sticky;
  top: 5%;
  h1 {
    text-align: left;
    font-size: 2em;
    letter-spacing: 1px;
    line-height: normal;
  }
  .subtitle {
    font-size: x-large;
  }
  .site-update time {
    font-weight: 600;
    display: block;
  }
  nav {
    ul {
      padding: 1em;
      li {
        ul {
          border: none;
          padding-bottom: 0;
          li {
            padding-bottom: 0;
          }
        }
        &::before {
          content: '୨୧';
          margin-right: .5em;
        }
      }
      &:first-child {
        border-bottom: 1px solid rgb(207, 145, 207);
        border-top: 1px solid rgb(207, 145, 207);
        padding: 1em;
      }
      &:nth-child(3) {
        padding-top: 0;
        padding-bottom: 0;
        margin-left: 1em;
        li {
          &::before {
            content: '⟶';
          }
        }
      }
    }
    .changelog {
      font-weight: 600;
    }
  }
}

nav li {
  display: block;
  padding-bottom: 1em;
}

main {
  width: clamp(500px, 35vw, 700px);
  margin: auto;
  margin-top: 3em;
}

article {
  & > section {
    border: 1px solid;
    padding: 2em;
    margin-bottom: 3em;
  }
}

section {
  ul {
    list-style-position: inside;
    padding: 0;
    ul {
      padding-top: 1em;
    }
  }
  li {
    border-bottom: 1px solid;
    padding: 1em;
    li {
      border: none;
      padding-bottom: 0;
    }
    &:first-child {
      padding-top: 0;
    }
  }
}

h2 {
  padding-bottom: 20px;
  padding-top: 0px;
  margin-top: 0px;
  border-bottom: 1px solid hotpink;
  font-size: 1.75em;
  text-transform: uppercase;
  letter-spacing: .1em;
}

h3 {
  margin-top: 0px;
  padding: .5em;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

h4 {
  text-transform: uppercase;
  letter-spacing: .1em;
  padding: .5em;
  &:before {
    content: '☆';
    margin-right: .5em;
  }
  &:after {
    content: '☆';
    margin-left: .5em;
  }
}

.archive-nav {
  ul {
    margin-left: 1em;
    display: initial;
    border: none;
    font-weight: 600;
  }
  li {
    margin-right: 0;
    font-weight: 600;
    border: none;
    padding: 0;
    display: initial;
    &::before {
      content: "⟶";
      margin-right: 0.5em;
      margin-left: 0.5em;
    }
    &::after {
      content: "•";
      margin-right: 0.5em;
      margin-left: 0.5em;
    }
    &:last-child {
      &::after {
        content: none;
      }
    }
    li {
      &::before {
        content: none;
      }
    }
  }
}

hr {
  display: block;
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: auto;
  margin-right: auto;
  border: 1px dashed plum;
}

button {
  background-color: rgba(255, 192, 239, 0.493);
  border: 1px solid purple;
  font-size: 1em;
  padding: .5em;
  border-radius: 5px;
}

footer {
  margin-left: auto;
  margin-right: auto;
  background-color: transparent;
  text-align: center;
  font-size: .9em;
  width: 80%;
  nav {
    border-bottom: 1px solid rgb(177, 137, 177);
    padding-bottom: 3em;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 3em;
    font-size: larger;
  }
  ul {
    padding: 0;
    list-style-type: none;
  }
  li {
    display: inline-block;
    margin-right: 15px;
    &:first-child {
      &::before {
        content: none;
      }
    }
    &::before {
      content: "⬩";
      font-size: 20px;
      margin-right: 15px;
    }
  }
  time {
    font-weight: 600;
  }
}

@include screen-768();
@include screen-1100();

:root {
  --light-body-bg: thistle;
  --light-font-1: #4e494e;
  --light-font-2: rgb(56, 36, 56);
  --light-main-bg: whitesmoke;
  --light-main-border: purple;
  --light-h1: #4a3b61;
  --light-h2: #5a5463;
  --light-h3: rgb(107, 72, 128);
  --light-h3-before: rgb(173, 123, 173);
  --light-h3-bg: rgb(227, 219, 230);
  --light-h4: color: rgb(119, 95, 35);
  --light-h4-bg: antiquewhite;
  --light-a: darkmagenta;
  --light-decor: rgb(169, 121, 179);
  --light-section-bg:  whitesmoke;
  --light-summary-bg: rgb(216, 216, 233);
  --light-li-border: rgb(223, 212, 212);
  --light-headernav: rgb(76, 15, 95);
}

@media (prefers-color-scheme: light) {
  body {
    color: var(--light-font-1);
    background-color: var(--light-body-bg);
  }
  article > section {
    background-color: var(--light-main-bg);
  }
  header {
    & p {
      color: var(--light-font-2);
    }
    & nav ul:first-child {
      color: var(--light-headernav);
    }
  }
  h1 {
    color: var(--light-h1);
  }
  h2 {
    color: var(--light-h2);
  }
  h3 {
    color: var(--light-h3);
    background-color: var(--light-h3-bg);
  }
  h3::before {
    color: var(--light-h3-before);
  }
  h4 {
    color: var(--light-h4);
    background-color: var(--light-h4-bg);
  }
  article::after {
    color: var(--light-decor);
  }
  section li {
    border-color: var(--light-li-border);
  }
  section > details summary {
    background-color: var(--light-summary-bg);
  }
  a {
    color: var(--light-a);
  }
}
.light-mode {
  color: var(--light-font-1);
  background-color: var(--light-body-bg);
  & article > section {
    background-color: var(--light-main-bg);
  }
  & header {
    & p {
      color: var(--light-font-2);
    }
    & nav ul:first-child {
      color: var(--light-headernav);
    }
  }
  & h1 {
    color: var(--light-h1);
  }
  & h2 {
    color: var(--light-h2);
  }
  & h3 {
    color: var(--light-h3);
    background-color: var(--light-h3-bg);
  }
  & h3::before {
    color: var(--light-h3-before);
  }
  & h4 {
    color: var(--light-h4);
    background-color: var(--light-h4-bg);
  }
  & article::after {
    color: var(--light-decor);
  }
  & section li {
    border-color: var(--light-li-border);
  }
  & section > details summary {
    background-color: var(--light-summary-bg);
  }
  & a {
    color: var(--light-a);
  }
  & a:hover {
    color: white;
  }
}

:root {
  --dark-a: pink;
  --dark-main-bg: rgb(34, 30, 30);
  --dark-li-border: purple;
}
@media (prefers-color-scheme: dark) {
  body {
    background-color: black;
    color: white;
  }
  article > section {
    background-color: var(--dark-main-bg);
    border-color: #4a3b61;
  }
  section li {
    border-color: var(--dark-li-border);
  }
  a {
    color: var(--dark-a);
  }
}
.dark-mode {
  background-color: black;
  color: white;
  & article > section {
    background-color: var(--dark-main-bg);
    border-color: #4a3b61;
  }
  & section li {
    border-color: var(--dark-li-border);
  }
  & a {
    color: var(--dark-a);
  }
  & header *, h2, h3, h4, summary {
    color: white;
  }
  & h3, h4, summary {
    background-color: transparent;
  }
}
::selection {
  background-color: rgb(255, 255, 191);
  color: black;
}
::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  &:hover {
    background-color: plum;
  }
}
::-webkit-scrollbar-thumb {
  background-color: pink;
  border-radius: 5px;
}